<template>
  <div class="">
    <el-card class="table">
      <el-form :model="queryParams" :rules="rules" ref="formRef">
        <el-form-item prop="title" label="">
          <el-input
            v-model="queryParams.title"
            clearable
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="description" label="">
          <el-input
            v-model="queryParams.description"
            clearable
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <el-tabs v-model="activeName">
        <el-tab-pane name="first" label="markdown">
          <!-- successFn -->
          <Markdown @success="successFn" ref="markdownRef"></Markdown>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script setup>
import { ref } from "vue";
import Markdown from "./components/Markdown.vue";// 
import { addArticle } from "@/api/article";// 
import { ElMessage } from "element-plus";// 
//  router.post('/addArticle', async (req, res) => {
//   const {
  // 
//     title,
//     description,
//     content
//   } = req.body
const queryParams = ref({
  title: "",
  description: "",
  content: "",
});
const activeName = ref("first");
const formRef = ref(null);
const rules = ref({
  title: [{ required: true, trigger: "blur", message: "" }],
  description: [{ required: true, trigger: "blur", message: "" }],
});
const markdownRef = ref(null);
//   
const successFn = (content) => {
  // content 
  // console.log(content);  
  //  22333   Markdown:<p>2332323</p>
  // queryParams.value.content
  queryParams.value.content = content;
  // 
  formRef.value.validate(async (value) => {
    if (value) {
      // 
      const { data } = await addArticle(queryParams.value);
      if (!data.code) {
        ElMessage.success(data.txt);  // 
        formRef.value.resetFields();  //  
        markdownRef.value.clearEditor();  // markdown 
      } else {
        // 
        ElMessage.error(data.txt);
      }
    }
  });
};
</script>

<style lang="scss" scoped>
:deep(.el-input) {
  width: 50% !important;
}
</style>
