<template>
  <div class="markdown-container">
    <div id="markdown-box" ref="boxRef"></div>
    <div class="bottom">
      <el-button type="primary" @click="submitFn"></el-button>
    </div>
  </div>
</template>
<script setup>
// npm i @toast-ui/editor
import { nextTick, ref, onMounted, defineExpose, defineEmits } from "vue";

import MKEditor from "@toast-ui/editor";// 
import "@toast-ui/editor/dist/toastui-editor.css";//
import "@toast-ui/editor/dist/i18n/zh-cn";//
import { ElMessage } from "element-plus";

const emit = defineEmits(["success"]); // 
const boxRef = ref(null);//dom
let mkEditor;//
// 1.
const initEditor = () => {
  mkEditor = new MKEditor({
    el: boxRef.value,// 
    height: "400px",    // 
    previewStyle: "vertical",  // 
    language: "zh-CN",  // 
  });
 
  mkEditor.getMarkdown(); // 
};
// 
const clearEditor = () => {
  mkEditor.reset();  // mkEditor 
};

// 
const submitFn = () => {
  // .getHTML() mkEditor 
  const htmlStr = mkEditor.getHTML();
  // console.log(htmlStr);<p><br></p>
  if (htmlStr === "<p><br></p>") {
    // 
    ElMessage.warning("");
  } else {
    //   
    emit("success", htmlStr);
  }
};
//2.  
onMounted(async () => {
  await nextTick(); // 
  initEditor(); //  
});
defineExpose({ clearEditor });
</script>
<style lang="scss" scoped>
.markdown-container {
  .bottom {
    margin-top: 20px;
    text-align: right;
  }
}
</style>
